import React, { useState, useRef } from 'react';
import '../css/Joker.css';
import Joker_Alcerium from './joker/Joker_Alcerium.js';
import Joker_Castorix from './joker/Joker_Castorix.js';
import Joker_Lyrisius from './joker/Joker_Lyrisius.js';
import Joker_Lysande from './joker/Joker_Lysande.js';
import Joker_Valterre from './joker/Joker_Valterre.js';
import arrow from '../img/arrow_white.png';
import alceriumIcon from '../img/alcerium_iconWhite.png';
import alceriumIconRed from '../img/alcerium_iconRed.png';
import castorixIcon from '../img/castorix_iconWhite.png';
import castorixIconRed from '../img/castorix_iconRed.png';
import lyrisiusIcon from '../img/lyrisius_iconWhite.png';
import lyrisiusIconRed from '../img/lyrisius_iconRed.png';
import lysandeIcon from '../img/lysande_iconWhite.png';
import lysandeIconRed from '../img/lysande_iconRed.png';
import valterreIcon from '../img/valterre_iconWhite.png';
import valterreIconRed from '../img/valterre_iconRed.png';

function Joker_Selection() {
    const [sector, showSector] = useState("None");

    function showAlcerium() {
        showSector("Alcerium");
    }
    function showCastorix() {
        showSector("Castorix");
    }
    function showLyrisius() {
        showSector("Lyrisius");
    }
    function showLysande() {
        showSector("Lysande");
    }
    function showValterre() {
        showSector("Valterre");
    }

    const sectorSelection = useRef(null);
    const toSectorSelection = () => 
    window.scrollTo({
        top: 0,
        behavior: "smooth"
    });

    const detailSection = useRef(null);
    const toDetailSection = () => 
    window.scrollTo({
        top: detailSection.current.offsetTop,
        behavior: "smooth"
    });

    if (sector == "None"){
        return (
            <div className="joker_selection">
                <div className="X">
                    <div className="joker_selectionR1">
                        <img src={lyrisiusIcon} className="jokerSectorIcons" onClick={showLyrisius}/>
                    </div>
                    <br></br><br></br><br></br>
                    <div className="joker_selectionR2">
                        <img src={alceriumIcon} className="jokerSectorIcons" onClick={showAlcerium}/>
                        <img src={valterreIcon} className="jokerSectorIcons" onClick={showValterre}/>
                        <img src={castorixIcon} className="jokerSectorIcons" onClick={showCastorix}/>
                    </div>
                    <br></br><br></br><br></br>
                    <div className="joker_selectionR3">
                        <img src={lysandeIcon} className="jokerSectorIcons" onClick={showLysande}/>
                    </div>
                </div>
            </div>
        )
    }

    else if (sector == "Alcerium"){
        return (
            <div>
                <div className="joker_selection" ref={sectorSelection}>
                    <div className="X">
                        <div className="joker_selectionR1">
                            <img src={lyrisiusIcon} className="jokerSectorIcons" onClick={showLyrisius}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR2">
                            <img src={alceriumIconRed} className="jokerSectorIcons" onClick={showAlcerium}/>
                            <img src={valterreIcon} className="jokerSectorIcons" onClick={showValterre}/>
                            <img src={castorixIcon} className="jokerSectorIcons" onClick={showCastorix}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR3">
                            <img src={lysandeIcon} className="jokerSectorIcons" onClick={showLysande}/>
                        </div>
                    </div>
                    <br></br><br></br><br></br>
                    <div className='divider'></div>
                    <div className="topInstruct">
                        <img src={arrow} className="arrow" />
                        <p className="jarrow_instruct">scroll to view alcerium affiliations</p>
                    </div>
                    <br></br>
                    <div className='divider'></div>
                    <Joker_Alcerium ref={detailSection}/>
                    <div className='divider2'></div>
                    <p className="arrow_instruct">return to joker sector selection</p>
                    <img src={arrow} className="upArrow" onClick={toSectorSelection}/>
                </div>
            </div>
        )
    }

    else if (sector == "Castorix"){
        return (
            <div>
                <div className="joker_selection" ref={sectorSelection}>
                    <div className="X">
                        <div className="joker_selectionR1">
                            <img src={lyrisiusIcon} className="jokerSectorIcons" onClick={showLyrisius}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR2">
                            <img src={alceriumIcon} className="jokerSectorIcons" onClick={showAlcerium}/>
                            <img src={valterreIcon} className="jokerSectorIcons" onClick={showValterre}/>
                            <img src={castorixIconRed} className="jokerSectorIcons" onClick={showCastorix}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR3">
                            <img src={lysandeIcon} className="jokerSectorIcons" onClick={showLysande}/>
                        </div>
                    </div>
                    <br></br><br></br><br></br>
                    <div className='divider'></div>
                    <div className="topInstruct">
                        <img src={arrow} className="arrow" />
                        <p className="jarrow_instruct">scroll to view castorix affiliations</p>
                    </div>
                    <br></br>
                    <div className='divider'></div>
                    <Joker_Castorix ref={detailSection}/>
                    <div className='divider2'></div>
                    <p className="arrow_instruct">return to joker sector selection</p>
                    <img src={arrow} className="upArrow" onClick={toSectorSelection}/>
                </div>
            </div>
        )
    }

    else if (sector == "Lyrisius"){
        return (
            <div>
                <div className="joker_selection" ref={sectorSelection}>
                    <div className="X">
                        <div className="joker_selectionR1">
                            <img src={lyrisiusIconRed} className="jokerSectorIcons" onClick={showLyrisius}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR2">
                            <img src={alceriumIcon} className="jokerSectorIcons" onClick={showAlcerium}/>
                            <img src={valterreIcon} className="jokerSectorIcons" onClick={showValterre}/>
                            <img src={castorixIcon} className="jokerSectorIcons" onClick={showCastorix}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR3">
                            <img src={lysandeIcon} className="jokerSectorIcons" onClick={showLysande}/>
                        </div>
                    </div>
                    <br></br><br></br><br></br>
                    <div className='divider'></div>
                    <div className="topInstruct">
                        <img src={arrow} className="arrow" />
                        <p className="jarrow_instruct">scroll to view lyrisius affiliations</p>
                    </div>
                    <br></br>
                    <div className='divider'></div>
                    <Joker_Lyrisius ref={detailSection}/>
                    <div className='divider2'></div>
                    <p className="arrow_instruct">return to joker sector selection</p>
                    <img src={arrow} className="upArrow" onClick={toSectorSelection}/>
                </div>
            </div>
        )
    }

    else if (sector == "Lysande"){
        return (
            <div>
                <div className="joker_selection" ref={sectorSelection}>
                    <div className="X">
                        <div className="joker_selectionR1">
                            <img src={lyrisiusIcon} className="jokerSectorIcons" onClick={showLyrisius}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR2">
                            <img src={alceriumIcon} className="jokerSectorIcons" onClick={showAlcerium}/>
                            <img src={valterreIcon} className="jokerSectorIcons" onClick={showValterre}/>
                            <img src={castorixIcon} className="jokerSectorIcons" onClick={showCastorix}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR3">
                            <img src={lysandeIconRed} className="jokerSectorIcons" onClick={showLysande}/>
                        </div>
                    </div>
                    <br></br><br></br><br></br>
                    <div className='divider'></div>
                    <div className="topInstruct">
                        <img src={arrow} className="arrow" />
                        <p className="jarrow_instruct">scroll to view lysande affiliations</p>
                    </div>
                    <br></br>
                    <div className='divider'></div>
                    <Joker_Lysande ref={detailSection}/>
                    <div className='divider2'></div>
                    <p className="arrow_instruct">return to joker sector selection</p>
                    <img src={arrow} className="upArrow" onClick={toSectorSelection}/>
                </div>    
            </div>
        )
    }

    else if (sector == "Valterre"){
        return (
            <div>
                <div className="joker_selection" ref={sectorSelection}>
                    <div className="X">
                        <div className="joker_selectionR1">
                            <img src={lyrisiusIcon} className="jokerSectorIcons" onClick={showLyrisius}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR2">
                            <img src={alceriumIcon} className="jokerSectorIcons" onClick={showAlcerium}/>
                            <img src={valterreIconRed} className="jokerSectorIcons" onClick={showValterre}/>
                            <img src={castorixIcon} className="jokerSectorIcons" onClick={showCastorix}/>
                        </div>
                        <br></br><br></br><br></br>
                        <div className="joker_selectionR3">
                            <img src={lysandeIcon} className="jokerSectorIcons" onClick={showLysande}/>
                        </div>
                    </div>
                    <br></br><br></br><br></br>
                    <div className='divider'></div>
                    <div className="topInstruct">
                        <img src={arrow} className="arrow" />
                        <p className="jarrow_instruct">scroll to view valterre affiliations</p>
                    </div>
                    <br></br>
                    <div className='divider'></div>
                    <Joker_Valterre ref={detailSection}/>
                    <div className='divider2'></div>
                    <p className="arrow_instruct">return to joker sector selection</p>
                    <img src={arrow} className="upArrow" onClick={toSectorSelection}/>
                </div>
            </div>
        )
    }
}

export default Joker_Selection;